import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { IManagedObject, InventoryService, IResultList } from '@c8y/client';
import { AlertService, gettext, ModalService } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { BehaviorSubject, Observable, pipe } from 'rxjs';
import { map, shareReplay, switchMap, tap } from 'rxjs/operators';
import { pathToDatapointLibrary } from './datapoint-library.model';
import { DatapointParserService } from '@c8y/ngx-components/datapoint-library/services';

@Component({
  selector: 'c8y-datapoint-library-list',
  templateUrl: './datapoint-library-list.component.html'
})
export class DatapointLibraryListComponent {
  listClass = '';
  isLoading = true;
  reload$: BehaviorSubject<void> = new BehaviorSubject(null);
  datapoints$: Observable<IResultList<IManagedObject>> = this.reload$.pipe(
    tap(() => {
      this.isLoading = true;
    }),
    switchMap(() => this.getDatapoints()),
    tap(() => {
      this.isLoading = false;
    }),
    shareReplay()
  );

  parsePipe = pipe(map((data: IManagedObject[]) => this.parser.parseDatapoints(data)));

  constructor(
    private inventory: InventoryService,
    private router: Router,
    private modalService: ModalService,
    private translateService: TranslateService,
    private alertService: AlertService,
    private parser: DatapointParserService
  ) {}

  getDatapoints(): Promise<IResultList<IManagedObject>> {
    return this.inventory.list({ pageSize: 20, fragmentType: 'c8y_Kpi', withTotalPages: true });
  }

  addDatapointEntry() {
    this.router.navigate([pathToDatapointLibrary, 'new']);
  }

  editDatapointEntry(entry: IManagedObject) {
    this.router.navigate([pathToDatapointLibrary, entry.id]);
  }

  async removeDatapointEntry(entry: IManagedObject) {
    try {
      await this.modalService.confirm(
        gettext('Delete data point'),
        this.translateService.instant(
          gettext('You are about to delete data point "{{ dpLabel }}". Do you want to proceed?'),
          { dpLabel: entry.c8y_Kpi.label }
        ),
        'danger'
      );
    } catch (e) {
      return;
    }
    try {
      await this.inventory.delete(entry.id);
      this.reload$.next();
    } catch (e) {
      this.alertService.addServerFailure(e);
    }
  }

  reload() {
    this.reload$.next();
  }
}
